@import url(https://www.gamergeeks.net/apps/minecraft/web-developer-tools/css-blocks-and-entities/icons-minecraft-0.46.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*:after,*:before{margin:0;padding:0}.sr-only{border:0 !important;clip:rect(1px, 1px, 1px, 1px) !important;-webkit-clip-path:inset(50%) !important;clip-path:inset(50%) !important;height:1px !important;margin:-1px !important;overflow:hidden !important;padding:0 !important;position:absolute !important;width:1px !important;white-space:nowrap !important}.fixedPosition{position:fixed;z-index:10}.item_icon{scale:1.25}.spacer{width:12px}.flex{display:flex;padding-top:5px;padding-left:8px}:root{--border: rgb(52, 95, 88)}ul,ol{list-style:none}body{background:var(--bg);font-family:"Segoe UI";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:14px;color:snow}h1,h2{height:18px}.wrapper{width:96%;max-width:1140px;margin:0 auto}.card-grid{margin:2em 0;display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:48px}.card{background-color:var(--bg-offset);padding:0px;box-shadow:0px 2px 4px rgb(52, 95, 88);box-shadow:0px 2px 4px var(--border);transition:all .3s cubic-bezier(0.075, 0.82, 0.165, 1)}.card:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.card:hover .card-content h2{display:block;-webkit-line-clamp:none;-webkit-box-orient:none;overflow:visible}.card-content{padding:0px 16px;padding-bottom:8px}.card-content h2{display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden}.card-list{margin-top:16px}.card-list li{color:#20b2aa;margin-top:8px}.card-list li span{color:#20b2aa}.card-header{margin-top:8px}.search-wrapper{margin:48px 0;display:flex;justify-content:space-between}.search-form{border:0px}.search-input{background-color:var(--bg-offset);background-repeat:no-repeat;padding:1.4em 2em;padding-left:2.7em;border:1px solid rgb(52, 95, 88);border:1px solid var(--border);color:var(--gray);box-shadow:0px 4px 6px rgb(52, 95, 88);box-shadow:0px 4px 6px var(--border);transition:all .4s cubic-bezier(0.215, 0.61, 0.355, 1)}.search-input:hover{box-shadow:0px 0px 0px rgb(52, 95, 88);box-shadow:0px 0px 0px var(--border)}.search-input:focus{box-shadow:0px 2px 3px rgb(52, 95, 88);box-shadow:0px 2px 3px var(--border)}select{-webkit-appearance:none !important;appearance:none !important;outline:none;background-color:var(--bg-offset);border-radius:.25em;border-width:1px;border-style:solid;border-color:rgb(52, 95, 88);border-color:var(--border);padding:1.4em 2em 1.4em 1em;margin:0;margin-right:1em;width:100%;font-family:inherit;font-size:inherit;cursor:inherit;line-height:inherit;width:100%;color:var(--gray)}.select{min-width:15ch;max-width:30ch;cursor:pointer;line-height:1.1;background-color:transparent;display:grid;grid-template-areas:"select";align-items:center;position:relative;box-shadow:0px 4px 6px rgb(52, 95, 88);box-shadow:0px 4px 6px var(--border);transition:all .4s cubic-bezier(0.215, 0.61, 0.355, 1)}.select:hover{box-shadow:0px 0px 0px rgb(52, 95, 88);box-shadow:0px 0px 0px var(--border)}.select::after{content:"";display:block;width:.8em;height:.5em;background-color:var(--text);-webkit-clip-path:polygon(100% 0%, 0 0%, 50% 100%);clip-path:polygon(100% 0%, 0 0%, 50% 100%);justify-self:end;margin-right:1em}.dropdown-option{background-color:#141414}.custom-select{background-color:#141414}select,.select:after{grid-area:select}select:focus+.focus{position:absolute;top:-1px;left:-1px;right:-1px;bottom:-1px;border:2px solid var(--text);border-radius:inherit}
